export const updateCandidate = async (reqData: any, candidateId: any) => {
    try {
      const response = await axios.put(`${API_URL}contacts/${candidateId}`, reqData, {
        method:"POST",
        headers: {
          'Content-Type': 'application/json',
          'X-Requested-With': 'XMLHttpRequest',
          Authorization: "Basic " + btoa(`${Email}:${Password}`),
        },
      });
      return response.data;  // Axios automatically parses the JSON response
    } catch (err : any) {
      // Axios throws an error for non-2xx status codes, you can access `err.response`
      return err.response ? err.response.data : err.message;
    }
  };
  
